
.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;

}

.Icon-container-check {
    position: absolute;
    left: -7px;

}

.Icon-container-check--active {
    color: #4caf50;

}

.Icon-container-delete {
    position: absolute;
    top: -30px;
    right: 0;
}

.Icon-container-delete:hover {
    color: red;
}
.Icon-container-dislike:hover {
    color: red;
}
.Icon-svg {
    width: 24px;
    height: 24px;
}
.Icon-container-check:hover .Icon-svg {
    fill: green;
}
.Icon-container-delete:hover .Icon-svg {
    fill: red;
}

.Icon-container-like:hover .Icon-svg {
    fill: blue;
}
.Icon-container-dislike:hover .Icon-svg {
    fill: red;
}
.btn {
    background-color: transparent;
    border: none;
}